
import {
  IonItem,
  IonContent,
  modalController,
  toastController,
} from "@ionic/vue";

import { defineComponent } from "vue";

export default defineComponent({
  name: "confrim",
  props: {
    // buyer: String,
    // grain: String,
    // month: String,
    // price: String,
    buyer: String,
    Month: String,
    Aprice: String,
    closed: String,
    crop: String,
  },
  components: {
    IonItem,
    IonContent,
  },
  data() {
    return {
      content: "Content",
    };
  },
  methods: {
    async fetchMessages() {
      console.log("Get data");
      // return []
      const mydata = {
        crop: this.crop,
        deliverieMonth: this.Month,
        buyer: this.buyer,
        //@ts-ignore
        contractAmount: Number(document.getElementById("amount").value),
      };
      const response = await fetch("https://farmer.weathon.top/api/creatRecord", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(mydata),
      });
      return response.ok;
    },
    async openToast() {
      const toast = await toastController.create({
        message: "Successfully confirmed.",
        duration: 2000,
        color: "dark",
        // translucent: true
      });
      return toast.present();
    },
    async c() {
      const ok = await this.fetchMessages(); //zhelijiumeiwenti huang gegechulai
      if (ok) {
        //kuohaowenti
        modalController.dismiss();
        this.openToast();
      } else {
        alert("Error!");
      }
    },
  },
});
